@import url(https://fonts.googleapis.com/css?family=Inconsolata);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    background-color: #28292a;
}

.app {
    font-family: Inconsolota;
    color: #aaa;
}

.highlighted {
    color: rgb(242, 93, 93);
}

.header {
    display: flex;
    flex-flow: row;
    justify-content: space-around;
    align-items: center;
}

.header h1 {
    padding: 0px;
    margin: 0px;
}

p.title {
    font-size: 28px;
    color: rgb(242, 93, 93);
}

.header .right_side a {
    padding: 10px;
    margin-right: 5px;
    text-decoration: none;
    color: #aaa;
    border-radius: 30px;
}
.header .right_side a:hover {
    background-color: black;
}

.header .right_side a.special {
    background-color: black;
}

.header .right_side .dropdown_btn {
    padding: 10px;
    background-color: black;
    border-radius: 10px;
    display: none;
}
.header .right_side .dropdown_btn:hover {
    background-color: #444;
}

.header .dropdown_menu {
    display: none;
}
.header .dropdown_menu_shown {
    position: absolute;
    display: flex;
    flex-flow: column nowrap;
    background-color: #111;
    right: 50px;
    top: 50px;
    overflow-x: hidden;
    overflow-y: hidden;
    border-radius: 20px;
    margin: 10px;
    width: 130px;
    height: 170px;
    animation: dropdown;
    animation-duration: 1s;
}
.header .dropdown_menu_shown a {
    padding: 10px;
    text-decoration: none;
    color: #aaa;
}
.header .dropdown_menu_shown .close {
    background-color: white;
    width: 100px;
}
.header .dropdown_menu_shown a:hover {
    background-color: #444;
}

.cards {
    display: flex;
    justify-content: space-around;
    flex-flow: row wrap;
    grid-row-gap: 40px;
    row-gap: 40px;
}

.resume_btn_container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.resume_btn {
    margin-top: 100px;
    margin-bottom: 100px;
    background-color: #111;
    padding: 10px;
    border-radius: 30px;
    font-size: 27px;
    text-decoration: none;
    color: #aaa;
}
.resume_btn:hover {
    background-color: #444;
    cursor: pointer;
}

@media only screen and (max-width: 700px) {
    .header .right_side a {
        display: none;
    }
    .header .right_side .dropdown_btn {
        display: block;
    }
}

@keyframes dropdown {
    from {
        width: 0px;
        height: 0px;
    }

    to {
        width: 130px;
        height: 170px;
    }
}

.section {
    margin-top: 100px;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    grid-column-gap: 60px;
    -webkit-column-gap: 60px;
            column-gap: 60px;
}

.section.wrap_reverse {
    flex-wrap: wrap-reverse;
}

.section .right {
    transform: rotate(2deg);
    max-width: 350px;
}

.section .left {
    max-width: 350px;
    transform: rotate(-2deg);
}

.bordered {
    border-radius: 30px;
    border-color: black;
    border-width: 3px;
    border-style: solid;
}

.second_section .section {
    flex-wrap: wrap-reverse;
}

@media only screen and (max-width: 700px) {
    .section .right {
        transform: none;
    }
    .section .left {
        transform: none;
    }
}

.tracks_container {
    display: flex;
    flex-flow: column nowrap;
    grid-gap: 5px;
    gap: 5px;
    padding: 10px;
    background-color: #28292a;
}

.track {
    display: flex;
    flex-flow: row nowrap;
    background-color: #111;
    padding: 10px;
    border-radius: 20px;
    grid-gap: 5px;
    gap: 5px;
}

.track img {
    width: 50px;
    height: 50px;
}

.track_info {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    align-items: flex-start;
}

.info_card {
    display: flex;
    flex-flow: column;
    align-items: center;
    text-align: center;
    max-width: 350px;
}

.info_card h2 {
    padding: 0px;
    margin: 0px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.info_card img {
    width: 250px;
    height: 250px;
}

